"use client"

import { useState, useCallback } from "react"
import { useRouter } from "next/navigation"
import { Button } from "@/components/ui/button"
import { Card } from "@/components/ui/card"
import Image from "next/image"

export function AudioUploader() {
  const router = useRouter()
  const [isDragging, setIsDragging] = useState(false)
  const [file, setFile] = useState<File | null>(null)
  const [isProcessing, setIsProcessing] = useState(false)
  const [error, setError] = useState<string | null>(null)

  const handleDragOver = useCallback((e: React.DragEvent) => {
    e.preventDefault()
    setIsDragging(true)
  }, [])

  const handleDragLeave = useCallback((e: React.DragEvent) => {
    e.preventDefault()
    setIsDragging(false)
  }, [])

  const handleDrop = useCallback((e: React.DragEvent) => {
    e.preventDefault()
    setIsDragging(false)
    
    const droppedFile = e.dataTransfer.files[0]
    if (validateFile(droppedFile)) {
      setFile(droppedFile)
      setError(null)
    }
  }, [])

  const handleFileSelect = useCallback((e: React.ChangeEvent<HTMLInputElement>) => {
    const selectedFile = e.target.files?.[0]
    if (selectedFile && validateFile(selectedFile)) {
      setFile(selectedFile)
      setError(null)
    }
  }, [])

  const validateFile = (file: File) => {
    const validTypes = ['audio/mpeg', 'audio/wav', 'audio/mp3', 'audio/ogg']
    if (!validTypes.includes(file.type)) {
      setError('Please upload a valid audio file (MP3, WAV, or OGG)')
      return false
    }
    if (file.size > 100 * 1024 * 1024) { // 100MB limit
      setError('File size must be less than 100MB')
      return false
    }
    return true
  }

  const handleProcess = async () => {
    if (!file) return

    try {
      setIsProcessing(true)
      setError(null)
      
      // Simulate processing delay
      await new Promise(resolve => setTimeout(resolve, 2000))
      
      // Navigate to results page (in a real app, you'd first upload and process the file)
      router.push('/results')
    } catch (err) {
      setError('An error occurred while processing the file')
    } finally {
      setIsProcessing(false)
    }
  }

  return (
    <Card className="p-8">
      <div
        className={`
          border-2 border-dashed rounded-lg p-8 text-center
          ${isDragging ? 'border-[#30D5C8] bg-[#30D5C8]/5' : 'border-gray-300'}
          ${!file ? 'cursor-pointer' : ''}
        `}
        onDragOver={handleDragOver}
        onDragLeave={handleDragLeave}
        onDrop={handleDrop}
      >
        {!file ? (
          <div className="space-y-4">
            <div className="relative w-16 h-16 mx-auto">
              <Image
                src="/file.svg"
                alt="Upload"
                fill
                className="object-contain"
              />
            </div>
            <div>
              <p className="text-lg font-medium">
                Drag and drop your audio file here
              </p>
              <p className="text-sm text-gray-500 mt-2">
                or click to select a file
              </p>
            </div>
            <input
              type="file"
              accept="audio/*"
              onChange={handleFileSelect}
              className="hidden"
              id="file-upload"
            />
            <Button
              variant="outline"
              onClick={() => document.getElementById('file-upload')?.click()}
            >
              Select File
            </Button>
          </div>
        ) : (
          <div className="space-y-4">
            <div className="relative w-16 h-16 mx-auto">
              <Image
                src="/file.svg"
                alt="File"
                fill
                className="object-contain"
              />
            </div>
            <p className="text-lg font-medium">{file.name}</p>
            <p className="text-sm text-gray-500">
              {(file.size / (1024 * 1024)).toFixed(2)} MB
            </p>
            <div className="flex justify-center gap-4">
              <Button
                variant="outline"
                onClick={() => {
                  setFile(null)
                  setError(null)
                }}
              >
                Remove
              </Button>
              <Button
                className="bg-[#30D5C8] hover:bg-[#2bc0b4]"
                onClick={handleProcess}
                disabled={isProcessing}
              >
                {isProcessing ? 'Processing...' : 'Process Audio'}
              </Button>
            </div>
          </div>
        )}
      </div>
      
      {error && (
        <div className="mt-4 p-4 bg-red-50 text-red-600 rounded-lg text-sm">
          {error}
        </div>
      )}
      
      <div className="mt-6 text-sm text-gray-500">
        <p>Supported formats: MP3, WAV, OGG</p>
        <p>Maximum file size: 100MB</p>
      </div>
    </Card>
  )
}
